/* WindEffect.module.css */
.wind-effect {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none; /* So it doesn't interfere with other elements */
  background-color: transparent; /* Ensures the background is transparent */
  -webkit-mask-image: radial-gradient(at center, black 0%, transparent 70%);
  z-index: -1;
  filter: blur(1px);
  // mix-blend-mode: soft-light;
}
