.command-prompt {
  border: 1px solid #ccc;
  padding: 10px;
  width: 150px; /* Adjust width as needed */
  cursor: pointer;
  transition: all 0.3s ease;

  background: rgb(255 255 255 / 50%);
  backdrop-filter: blur(12px);

  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 16px);
  border-radius: 10px;
  box-shadow: 0px 0px 0px 1px hsla(0,0%,100%,0.04) inset, 0px 4px 8px 0px hsla(0,0%,0%,0.2);
  pointer-events: auto;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.command-prompt.active {
  width: 50%;
}

.command-prompt:focus {
  outline: none; /* Removes the default focus outline */
}

.cursor {
  display: inline-block;
  background-color: black;
  /* animation: blink 1s steps(1) infinite; */

  width: 3px;
  height: 16px;
  border-radius: 2px;
  background-color: rgb(0 0 0 / 50%);
  transition: opacity 0.5s ease-out 0s;

  animation: blink 1s step-end infinite;
  animation-delay: 4s; /* Wait for 4 seconds before starting */
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}

.cursor.hidden {
  display: none;
}

.options {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 100%; /* Positioned right below the command box */
  left: 0;
  width: 100%;
  background: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: for a drop-shadow effect */
  z-index: 1000; /* Ensure it stacks on top */
}

.options li {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.options li.selected {
  background-color: #e0e0e0; /* Highlight color for selected item */
}

.options li:hover {
  background-color: #f0f0f0; /* Hover color */
}

#command-icon {
  width: 20px; /* Or the size you want */
  height: 20px; /* Or the size you want */
  vertical-align: middle; /* Aligns icon with text if there is any */
  fill: rgb(0 0 0 / 50%);
  
}