.youtube-player {
    position: relative;
    top: 2px;
    --width: 32px;
    --height: 32px;
    width: var(--width);
    height: var(--height);
    -webkit-mask-image: radial-gradient(at center, black 0%, transparent 70%);
    pointer-events: none;
    transition: opacity 0.5s ease-in-out;
}
#youtube-player-instance {
    width: var(--width);
    height: var(--height);
}