/* Timer.css */
.timer {
  position: absolute;
  left: 50%;
  bottom: 16px;
  transform: translateX(-50%);
  border-radius: 5px;
  pointer-events: none;
  min-width: 50vw;
}

.timer-display {
  font-size: 1.5rem;
  color: #344136;
  user-select: none;
}

.timer-controls {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 8px;
}

.timer-controls button {
  padding: 5px 10px;
  /* margin: 0 8px; */
  cursor: pointer;
  font-size: 1rem;
  pointer-events: all;
  user-select: none;
}

.quote {
  transition: opacity 0.5s ease-in-out;
  text-align: center;
  /* margin-top: 16px; */
  min-height: 48px;
  display: flex;
  align-content: center;
  flex-direction: column;
  pointer-events: all;
  /* user-select: none; */
}

.fadeIn {
  opacity: 1;
}

.fadeOut {
  opacity: 0;
}

.timer-background {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.5);
  -webkit-mask-image: radial-gradient(at center bottom, black 0%, black 50%, transparent 100%);
  /* mask-image: radial-gradient(at center bottom, black 0%, black 50%, transparent 100%); */
  width: 100%;
  height: 100%;
  z-index: -2;
  pointer-events: none;
  backdrop-filter: blur(16px);
  border-radius: 14px;
}

.timer-contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 16px;
}

.timer-header {
  display: flex;
  display: inline-flex;
  align-items: center;
  gap: 16px;
}
.diamond-container {
  display: flex;
  display: inline-flex;
  align-items: center;
  gap: 4px;
}


.diamond-mask {
  --size: 24px;
  width: var(--size); /* Set the width you want */
  height: var(--size); /* Set the height you want */

  background-color: var(--category-color, #50C878);
  filter: brightness(0.9);

  -webkit-mask-image: url('./Timer/diamond.png');
  mask-image: url('./Timer/diamond.png');
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-position: center;
  mask-position: center;
  top: 1px;
  position: relative;
}

.category-name {
  color: var(--category-color, #50C878);
}

.break {
  flex-basis: 100%;
  height: 0;
}